
import semih from '../../../../components/semih.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import service_getData from '../../../../service/getData.js'
import service_upData from '../../../../service/upData.js'

export default{
components:{
semih,
XHeader, XInput, Checker, CheckerItem, Group, Calendar, Cell, Checklist,Toast
},
filters: {
toSemih(index, timeDate){
if(index == -1){
return '--'
}
return timeDate[index]
}
},
data(){
return{
select_machine_load:false, // 
b_shade_box:true, // 
dateValue: GetDateStr(new Date(), 0), // 'TODAY', // 
o_select_machine:[1], // 
toast_load:false,
re_page:false, // 
toast_text:'', // 
myArr_service1:[], // 1
myArr_service2:[], // 1
sevOtherArr1:[], //  [16,20,24,25,26]
sevOtherArr2:[],
samples:[], // 
books: [
{ // 
"freezeStartTime":149839898877, // 
"instrumentId": 1, // 
"startIndex":-1,
"endIndex":-1,
"cells": [
16,17 //  00:00-0:30 10:30-1:00 
]
},
{
"freezeStartTime":149839898877,
"instrumentId": 2,
"startIndex":-1,
"endIndex":-1,
"cells": [
16,17
]
}
]
}
},
created(){

// 
service_getData.booking({ // 
timeStart: +new Date().setHours(0, 0, 0, 0),
timeEnd: +new Date().setHours(24, 0, 0, 0)
}).then(res=>{
var date1=[];
var date2=[];
for(var i=0;i<res.length;i++){
if( res[i].instrumentId == '1' ){ // 11
for(var j=0; j<res[i].bookingDto.length; j++){
// console.log(1,res[i].bookingDto[j])
if( res[i].bookingDto[j].booked ){ // 
date1.push(res[i].bookingDto[j].timeflag)
}
}
}
if( res[i].instrumentId == '2' ){             for(var j=0; j<res[i].bookingDto.length; j++){
// console.log(1,res[i].bookingDto[j])
if( res[i].bookingDto[j].booked ){ // 
date2.push(res[i].bookingDto[j].timeflag)
}
}
}
}

this.sevOtherArr1=date1 // [16,17,19]
this.sevOtherArr2=date2
this.select_machine_load=true
})

// 
var samples = this.$store.state.allSample
for(var i=0;i<samples.length;i++){
this.samples.push({
name:samples[i].name,
category:samples[i].key.key,
})
}
},
computed:{
curDate(){
return GetDateStr(new Date(), 0)
},
dateText(){
var mydate=new Date(this.dateValue);
var mydate2=
(mydate.getMonth()+1)+""+
mydate.getDate()+" "+
" "+['','','','','','',''][mydate.getDay()];
console.log(mydate2);
return mydate2
},
day(){
var data=new Date();
data.getMonth()+1;
var day=data.getMonth()+1+''
return data.getMonth()+1
},
week(){
var data=new Date();
return data.getMonth()+1
},
},
methods:{
re_page_fn(){
if( this.re_page ){
this.$router.push({name:'application',params:{type:'forzen'}});
}
},
calendar_change_fn(){
this.select_machine_load=false

service_getData.booking({ // 
timeStart: +new Date(this.dateValue).setHours(0, 0, 0, 0),
timeEnd: +new Date(this.dateValue).setHours(24, 0, 0, 0)
}).then(res=>{
var date1=[];
var date2=[];
for(var i=0;i<res.length;i++){
if( res[i].instrumentId == '1' ){ // 11
for(var j=0; j<res[i].bookingDto.length; j++){
// console.log(1,res[i].bookingDto[j])
if( res[i].bookingDto[j].booked ){ // 
date1.push(res[i].bookingDto[j].timeflag)
}
}
}
if( res[i].instrumentId == '2' ){             for(var j=0; j<res[i].bookingDto.length; j++){
// console.log(1,res[i].bookingDto[j])
if( res[i].bookingDto[j].booked ){ // 
date2.push(res[i].bookingDto[j].timeflag)
}
}
}
}

this.sevOtherArr1=date1
this.sevOtherArr2=date2
this.select_machine_load=true
})

},
getMyArr_service1(res){
// 
// var temp=res.slice(0)
// temp.length=temp.length-1
// this.myArr_service1 = temp

// 
this.myArr_service1 = res
},
getMyArr_service2(res){
// 
var temp=res.slice(0)
temp.length=temp.length-1
this.myArr_service2 = temp
},
click_machine(item){
console.log(item)
},
lastDay(){
this.dateValue=GetDateStr(new Date(this.dateValue), -1);
},
nextDay(){
this.dateValue=GetDateStr(new Date(this.dateValue), 1);
},
submit(){
console.log(this.o_select_machine[0]==1, this.myArr_service1.length>0)
var books=[];

// 
if(this.o_select_machine.length==2 && this.myArr_service1.length>0  && this.myArr_service2.length>0 ){
// alert('1,2')
books=[
{
"freezeStartTime":+new Date(this.dateValue), // 
"instrumentId": 1, // 
"cells": this.myArr_service1 //  00:00-0:30 10:30-1:00 
},
{
"freezeStartTime":+new Date(this.dateValue),
"instrumentId": 2,
"cells": this.myArr_service2
}
]

}else{
if(this.o_select_machine[0]==1 && this.myArr_service1.length>0){
// alert('1')

books=[
{
"freezeStartTime":+new Date(this.dateValue),
"instrumentId": 1,
"cells": this.myArr_service1
}
]

}else if(this.o_select_machine[0]==2 && this.myArr_service2.length>0){
// alert('2')

books=[
{
"freezeStartTime":+new Date(this.dateValue),
"instrumentId": 2,
"cells": this.myArr_service2
}
]

}else{
this.toast_text=''
this.toast_load=true
return
}

}

var id=null;

try{
id = this.$store.state.applicant.default.id
}catch(e){
this.toast_text=''
this.toast_load=true
return
}


var obj={
"applicantId": id, // #ID
"researchType":2, // # 1- 2-
"samples": this.samples,
"books": books
}
console.log(obj)
// 
service_upData.application_type2(obj).then(res=>{
this.toast_text=''
this.toast_load=true
this.re_page=true
},res=>{
this.toast_text=''
this.toast_load=true
})

}
},
mounted: function () {}
}
function GetDateStr(d1,AddDayCount) {
var dd = d1;
dd.setDate(dd.getDate()+AddDayCount);//AddDayCount
var y = dd.getFullYear();
var m = dd.getMonth()+1;//
var d = dd.getDate();
y<=9 ? y='0'+y : '';
m<=9 ? m='0'+m : '';
d<=9 ? d='0'+d : '';
return y+"-"+m+"-"+d;
}



